import Vue from 'vue'
import Router from 'vue-router'
import haa from '@/components/haa'
import hab from '@/components/hab'
import index from '@/components/index'

Vue.use(Router)

export default new Router({
  routes: [
  	{
      path: '/',
      redirect: '/haa',
    },
    {
      path: '/',
      name: 'index',
      component: index,
      children: [
	      {
	        path: 'haa',
	        component: haa
	    	},
	    	{
	        path: 'hab',
	        component: hab
	    	},
      ]
    }
  ]
})



