	    			</tr>
		    	</table>
		    </div>
		    </div>
		  </el-collapse-item>
  	</el-collapse>
  </div>
</template>
<script>
  export default {
    name: 'haa',
    data() {
      return {
      	showcom:false,
      	currentPage: 5,
      	activeName: '0',
      	foot:false,
      	db:'',
      	legname:'',
      	page:'',
      	pagesize:'',
      	selected:1,
      	options:[],
      	ad:1
      }
    },
    created () {
	    this.get();
	  },
    methods:{
      get(){
				var vim = this;
				vim.axios.get("static/data.json")
				.then(function(res){
					vim.db = res.data;
					vim.pagesize = res.data.legname.length;
					//每页5条，共多少页
					vim.page = Math.ceil(vim.pagesize/5);
					//给options添加页数值
					for(let i=1; i <= vim.page; i++){
						vim.options.push(i);
					}
					if(vim.ad < vim.page){
							vim.ad = vim.ad*5;
							vim.ad++;
						}
				}).catch(function(error){
					console.log(error);
				});
			},
			onClick:function(){
				this.showcom = true;
			}
    },
    computed: {
		  filteredDb: function () {
		    return this.db.legname.slice(0, 5)
		  },
		  filteredIte: function () {
		    return this.db.db.slice(0, 5)
		  }
		  
		},
    directives: {
	    focus: {
	      inserted: function (el) {
	        el.focus()
	      }
	    }
	  }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>


